// 
.banner-bg {
  background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week6-202207/bg_home_mobile.jpg?raw=true);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  width: 100%;
  padding: 84px 0;

  &-content {
    margin: 0 auto;
    text-align: center;
    position: relative;
    max-width: 650px;
    // left: 50%;
    // top: 50%;
    // transform: translate(-50%, -50%);

    &:before {
      display: inline-block;
      position: absolute;
      content: "";
      width: 10px;
      height: 10px;
      background-color: #fff;
      left: -5px;
      top: -5px;
    }

    &:after {
      display: inline-block;
      position: absolute;
      content: "";
      width: 10px;
      height: 10px;
      background-color: #fff;
      right: -5px;
      top: -5px;
    }
  }
}

@media (min-width: 768px) {
  .banner-bg {
    background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week6-202207/bg_home.jpg?raw=true);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    width: 100%;
    padding: 216px 0;
  }
}

// 
.banner {
  padding: 47px 80px;
  border: 1px solid #fff;
}

.square {
  width: 10px;
  height: 10px;
  background-color: #fff;
}

// END

// 
.intro {

  // 
  &-title {
    text-align: center;

    @media (min-width: 992px) {
      .intro-title {
        text-align-last: left;

        &:after {
          left: 0;
          transform: translate(0%, 0%);
        }
      }
    }

    &:after {
      display: inline-block;
      position: absolute;
      content: "";
      background-color: $secondary;
      width: 50px;
      height: 3px;
      left: 50%;
      bottom: -16px;
      transform: translate(-50%, -50%);
    }
  }

  // 
  &-item {
    background: $primary;
    border: 1px solid $primary;
    text-align: center;
    color: $light;
    padding: 48px 0;

    &:hover {
      background-color: $navy-dark;
      border: 1px solid $secondary;
      border-radius: 12px;

      h3 {
        color: white;
      }

      span {
        background: $secondary;
        background-clip: text;
        color: transparent;
      }
    }
  }
}

// 
.step {
  &-item {
    border: 1px solid $light;
    border-radius: 12px;
    color: $light;
  }

  &-number {
    position: absolute;
    left: 48px;
    top: -32px;
    width: 78px;
    text-align: center;
    font-size: 48px;
    font-weight: bold;
    color: $secondary;
    background-color: $navy;
    font-family: "Azeret Mono";
  }
}
// END

// 
.lessonTag {
  overflow: hidden;
  font-weight: bold;
  color: $light;

  a {
    display: block;

    &:hover {
      background-color: $secondary;
      color: $light;
    }
  }
}
// END


// 
.hotLesson {

  // 
  .card {
    background: $primary;
    border: 2px solid $primary;
    border-radius: 24px;

    &:hover {
      border: 2px solid $secondary;
    }
  }

  // 
  &-btn {
    display: block;
    width: 172px;
    margin: 0 auto;
    background: $navy-dark;
    border: 1px solid $light;
    font-size: 16px;
    text-align: center;
    color: $light;

    &:hover{
      background: $secondary;
      border: 1px solid $secondary;
      color: $navy-dark;
    }
  }
}

// END


