.feedback {
  background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week6-202207/bg_share.jpg?raw=true);
  height: 733px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 40px 0;

  @media (min-width:992px) {
    padding: 80px 0;
  }
}

.swiper {
  width: 100%;
  height: 100%;

  @media (min-width:992px) {
    height: 429px;
  }

  &-img {
    position: relative;
    width: calc(100% - 5px);
    left: 5px;
    height: 213px;
    border-radius: 24px;
    margin-bottom: 40px;

    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      border: 1px solid #7C7E77;
      border-radius: 24px;
      left: -5px;
      top: 5px;
      z-index: -1;

      @media (min-width:992px) {
        left: -10px;
        top: 10px;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 24px;
    }

    @media (min-width:992px) {
      position: absolute;
      width: 60%;
      height: 90%;
      top: 0;
      left: 10px;
    }
  }

  .card {
    width: 45%;
    padding: 32px;
    border-radius: 24px 24px 24px 0;
    bottom: 0;
    right: 0;
    position: absolute;

    h3 {
      font-size: 18px;
    }

    &-img {
      border-radius: 50%;
      width: 100px;
      height: 100px;
      position: absolute;
      right: 32px;
      bottom: 100%;
      transform: translateY(50%);

      @media (max-width:991px) {
        width: 80px;
        height: 80px;
        right: 24px;
        top: -30px;
        bottom: auto;
        transform: none;
      }
    }

    @media (max-width:991px) {
      position: relative;
      width: 100%;
      padding: 24px;
      margin-bottom: 32px;
    }
  }

  .swiper-button-next:after,
  .swiper-button-prev:after {
    content: '';
  }

  &-button-next,
  &-button-prev {
    position: unset;
    width: 24px;
    height: 24px;
    margin-top: 0;
    color: #62DB54;
  }

  .swiper-pagination {
    position: unset;
    width: auto;

    .swiper-pagination-bullet {
      background-color: #62DB54;
      opacity: initial;

      &-active {
        width: 16px;
        border-radius: 100px;
      }
    }
  }

  &-buttons {
    bottom: 0;
    left: 30%;
    transform: translateX(-50%);
    z-index: 10;
    position: absolute;

    @media (max-width: 991px) {
      position: initial;
      transform: none;
    }
  }
}

// END